<template>
  <div class="w-divider">
    <slot></slot>
  </div>
</template>
<style lang="less">
  @import '~assets/styles/var.less';
  .w-divider {
    display: table;
    white-space: nowrap;
    height: auto;
    margin-bottom: 10px;
    overflow: hidden;
    line-height: 1;
    text-align: center;
    padding: 10px 0;
    color: #999;
    font-size: 12px;
  }

  .w-divider:after,
  .w-divider:before {
    content: '';
    display: table-cell;
    position: relative;
    top: 50%;
    width: 50%;
    background-repeat: no-repeat;
    background-image: url(data:image/png;base64,)
  }

  .w-divider:before {
    background-position: right 1em top 50%
  }

  .w-divider:after {
    background-position: left 1em top 50%
  }
</style>
<script>
  export default {
    name: 'Divider'
  }
</script>



